import Head from "next/head"

export default function SEO({
  /**
   * @var string
   */
  title,
  /**
   * @var string
   */
  description,
  /**
   * @var array
   */
  meta = [],
}) {
  if (description) {
    meta = meta.concat([
      {
        name: "description",
        content: description,
      },
    ])
  }
  return (
    <Head>
      {title ? <title>{title}</title> : null}
      {meta.map((props, i) => (
        <meta key={i} {...props} />
      ))}
    </Head>
  )
}
